import React from 'react';
import PropTypes from 'prop-types';
import Tutorial from './Tutorial';
import Header from './layout/Header';
import Sidebar from './layout/Sidebar';
import GithubIcon from './layout/GithubIcon';
import menu from './menu';

require('./Docs.css');

const routes = [];
for (let i = 0, len = menu.length; i < len; i += 1) {
  for (let j = 0, jlen = menu[i].links.length; j < jlen; j += 1) {
    routes[menu[i].links[j].href] = menu[i].links[j];
  }
}

const Docs = (props) => {
  const link = routes[props.link];

  return (
    <div className="docs">
      <Sidebar handleLink={props.handleLink} />
      <div className="social-buttons">
        <div
          className="fb-share-button"
          data-href="https://lucasbassetti.com.br/react-simple-chatbot/"
          data-layout="button"
          data-mobile-iframe="true"
        >
          <a
            className="fb-xfbml-parse-ignore"
            target="_blank"
            href="https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Flucasbassetti.com.br%2Freact-simple-chatbot%2F&amp;src=sdkpreparse"
          >
            Compartilhar
          </a>
        </div>
        <a
          href="https://twitter.com/share"
          className="twitter-share-button"
          data-text="Simple chatbot / conversational-ui React component"
          data-url="https://lucasbassetti.com.br/react-simple-chatbot"
          data-hashtags="react"
        >
          Tweet
        </a>
      </div>
      <GithubIcon />
      <main>
        {
          link ? (
            <div>
              <Header
                title={link.title}
                description={link.description}
              />
              { link.component }
            </div>
          ) : (
            <Header
              title="404 - Not Found"
              description="Page not found"
            />
          )
        }
      </main>
      <Tutorial handleLink={props.handleLink} />
    </div>
  );
};

Docs.propTypes = {
  link: PropTypes.string.isRequired,
  handleLink: PropTypes.func.isRequired,
};

export default Docs;



